<template>
    <div>

        <el-page-header style="margin-top: 20px" @back="back" content="">
        </el-page-header>
        <el-table
                style="margin-top: 20px;"
                :data="result"
                height="100%"
                stripe
                @selection-change="handleSelectionChange"
        >
            <el-table-column type="selection" width="50"></el-table-column>
            <el-table-column prop="id" label="ID" width="50px" align="right"></el-table-column>
            <el-table-column prop="name" label="" width="250px"></el-table-column>
            <el-table-column prop="sex" label="">
            </el-table-column>
            <el-table-column prop="year" label=""></el-table-column>
            <el-table-column prop="idCard" label=""></el-table-column>
            <el-table-column prop="tel" label="" width="50px" align="center"></el-table-column>
            <el-table-column prop="addr" label="" width="50px" align="center"></el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.row,scope.$index)">
                    </el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row,scope.$index)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="" :visible.sync="editVisible">
            <p style="color: red"></p>
            <p></p>
            <table class="el-table">
                <tr>
                    <td class="table-header"></td>
                    <td class="table-header"></td>
                    <td class="table-header">
                        
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editUser.name}}</td>
                    <td>
                        <el-input clearable :placeholder="editUser.name" v-model="changeItems.name"></el-input>
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>
                        {{editUser.gender===0?'':''}}
                    </td>
                    <td>
                        <el-radio-group v-model="changeItems.gender">
                            <el-radio label="0"></el-radio>
                            <el-radio label="1"></el-radio>
                        </el-radio-group>
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editUser.idCard}}</td>
                    <td>
                        <el-input clearable :placeholder="editUser.idCard" v-model="changeItems.idCard"></el-input>
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editUser.tel}}</td>
                    <td>
                        <el-input clearable :placeholder="editUser.tel"
                                  v-model="changeItems.tel"></el-input>
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editUser.addr}}</td>
                    <td>
                        <el-input clearable :placeholder="editUser.addr" type="number"
                                  v-model="changeItems.addr"></el-input>
                    </td>
                </tr>
            </table>
            <div style="text-align: center">
                <el-button type="primary" @click="handleUpdate"></el-button>
                <el-button type="danger" @click="changeItems={...emptyChangeItems}"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "UserResult",
        data() {
            return {
                result: [],
                editUser: {},
                changeItems: {},
                editVisible: false,
                emptyChangeItems: {}
            }
        },
        methods: {
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            back() {
                this.$router.go(-1);
            },
            handleEdit(row, index) {
                this.editVisible = true
                this.editUser = this.result[index]
                this.emptyChangeItems = {
                    id: row.id,
                    name: '',
                    gender: '',
                    tel: '',
                    addr: '',
                    idCard:'',
                    year:new Date().getFullYear()
                }
                this.changeItems = {...this.emptyChangeItems}
            },
            handleDelete(row, index) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete('/reader/' + row.id).then((response) => {
                        // console.log(response.data)
                        alert('' + response.data + '')
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                    }).catch((error) => {
                        this.$message.error(": -> " + JSON.stringify(error))
                    })

                    this.result.splice(index, 1)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            handleUpdate() {
                let data = this.changeItems;
                console.log(data)
                this.axios.put('/reader/' + data.id, data)
                    .then((response) => {
                        alert('' + response.data + '')
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                        this.editVisible=false

                    })
                    .catch((error) => {
                        this.$message({
                            type: 'error',
                            message: ": -> " + JSON.stringify(error)
                        })
                    })
            }
        },
        mounted() {
            let stateResult = this.$store.state.userQueryResult;
            stateResult=stateResult.map(elem=> {
                elem.sex=elem.gender===0?'':''
                return elem
            })
            this.result = stateResult
        }
    }
</script>

<style scoped>
    .table-header {
        color: darkslategrey;
        font-weight: bold;
    }
</style>