<template>
    <div>
        <el-page-header style="margin-top: 20px" @back="back" content="">
        </el-page-header>

        <p style="color:gray">0</p>
        <el-table
                style="margin-top: 20px;"
                :data="result"
                stripe
                @selection-change="handleSelectionChange"
        >
            <el-table-column type="selection" width="50"></el-table-column>
            <el-table-column prop="id" label="ID" width="50px" align="center"></el-table-column>
            <el-table-column prop="readerId" label="ID" width="50px" align="center"></el-table-column>
            <el-table-column prop="readerName" label="" width="100px" align="left"></el-table-column>
            <el-table-column prop="bookId" label="ID" width="50px" align="center"></el-table-column>
            <el-table-column prop="bookName" label="" align="left"></el-table-column>
            <el-table-column prop="borrowTime" label="" width="250px" align="left"></el-table-column>
            <el-table-column prop="returnBorrow" label="" width="50px" align="center"></el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.row,scope.$index)">
                    </el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row,scope.$index)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="" :visible.sync="editVisible">
            <p style="color: red"></p>
            <p></p>
            <table class="el-table">
                <tr>
                    <td class="table-header"></td>
                    <td class="table-header"></td>
                    <td class="table-header">
                        
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editBorrow.borrowTime}}</td>
                    <td>
                        <el-input clearable type="datetime-local" :placeholder="editBorrow.borrowTime" v-model="changeItems.borrowTime"></el-input>
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editBorrow.returnBorrow}}</td>
                    <td>
                        <el-input clearable type="number" :placeholder="editBorrow.returnBorrow" v-model="changeItems.returnBorrow"></el-input>
                    </td>
                </tr>
            </table>
            <div style="text-align: center">
                <el-button type="primary" @click="handleUpdate"></el-button>
                <el-button type="danger" @click="changeItems={...emptyChangeItems}"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "BorrowResult",
        data() {
            return {
                result: [],
                editBorrow: {},
                changeItems: {},
                editVisible: false,
                emptyChangeItems: {}
            }
        },
        methods: {
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            back() {
                this.$router.go(-1);
            },
            handleEdit(row, index) {
                this.editVisible = true
                this.editBorrow = this.result[index]
                this.emptyChangeItems = {
                    id: row.id,
                    borrowTime:'',
                    returnBorrow:null
                }
                this.changeItems = {...this.emptyChangeItems}
            },
            handleDelete(row, index) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete('/borrow/' + row.id).then((response) => {
                        // console.log(response.data)
                        alert('' + response.data + '')
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                    }).catch((error) => {
                        this.$message.error(": -> " + JSON.stringify(error))
                    })

                    this.result.splice(index, 1)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            handleUpdate() {
                let data = this.changeItems;
                console.log(data)
                this.axios.put('/borrow/' + data.id, data)
                    .then((response) => {
                        alert('' + response.data + '')
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                        this.editVisible=false

                    })
                    .catch((error) => {
                        this.$message({
                            type: 'error',
                            message: ": -> " + JSON.stringify(error)
                        })
                    })
            },
            queryAllResult() {
                let self = this
                let url = '/borrow'
                axios.get(url).then(function (response) {
                    if (response['status'] === 200) {
                        self.result=response['data']
                    }
                }).catch((error)=>alert(JSON.stringify(error)))
            },
            queryResultByConditions() {
                //

                this.result=this.$store.state.borrowParameter
            },
        },
        mounted() {
            if(this.$route.path==='/admin/borrow') {
                this.queryAllResult()
            }else if(this.$route.path==='/admin/borrow/result'){
                this.queryResultByConditions()
            }
        }
    }
</script>

<style scoped>
    .table-header {
        color: darkslategrey;
        font-weight: bold;
    }
</style>