<template>
    <div class="admin-form">
        <el-form label-position="right" :model="user">
            <el-form-item label="">
                <el-input clearable v-model="user.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="user.gender">
                    <el-radio label="0"></el-radio>
                    <el-radio label="1"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
                <el-input clearable v-model="user.idCard" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input clearable v-model="user.tel" placeholder="/"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input clearable v-model="user.addr" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input disabled clearable v-model="user.year" placeholder=""></el-input>
            </el-form-item>
            <el-button type="primary" @click="handleSubmit"></el-button>
            <el-button @click="user={...emptyUser}"></el-button>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "UserRegister", data() {
            return {
                user: {
                    name: '',
                    gender: '',
                    tel: '',
                    addr: '',
                    idCard:'',
                    year:new Date().getFullYear()
                },
                emptyUser: {
                    name: '',
                    gender: '',
                    tel: '',
                    addr: '',
                    idCard:'',
                    year:new Date().getFullYear()
                }
            }
        },
        methods:{
            handleSubmit(){
                console.log(this.user)
                this.axios.post('/reader',JSON.stringify(this.user))
                    .then((response) => {
                        alert('' + response.data + '')
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                    }).catch((error) => {
                    this.$message.error(": -> " + JSON.stringify(error))
                })
            }
        }
    }
</script>

<style scoped>

</style>