<template>
    <div>
        <p>ID</p>
        <el-form :model="borrow" label-position="right">
            <el-form-item label="">
                <el-input v-model="borrow.bookId" clearable placeholder="">
                </el-input>
            </el-form-item>
            <p></p>
            <el-form-item label="">
                <el-input v-model="borrow.idCard" clearable placeholder="">
                </el-input>
            </el-form-item>
            <el-button type="primary" @click="handleReturn"></el-button>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "ReturnBook",
        data() {
            return {
                borrow: {
                    bookId:"",
                    returnBorrow:0,
                    idCard:''
                }
            }
        },
        methods: {
            handleReturn() {
                this.axios.put('/borrow/return', this.borrow)
                    .then((response) => {
                        alert('' + response.data + '')
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                    }).catch((error) => {
                    this.$message.error(": -> " + JSON.stringify(error))
                })

            }
        }
    }
</script>

