<template>
    <div>

        <el-page-header style="margin-top: 20px" @back="back" content="">
        </el-page-header>
        <el-table
                style="margin-top: 20px;"
                :data="result"
                height="100%"
                stripe
                @selection-change="handleSelectionChange"
        >
            <el-table-column type="selection" width="50"></el-table-column>
            <el-table-column prop="id" label="ID" width="50px" align="right"></el-table-column>
            <el-table-column prop="name" label="" width="250px"></el-table-column>
            <el-table-column prop="author" label=""></el-table-column>
            <el-table-column prop="publishing" label=""></el-table-column>
            <el-table-column prop="isbn" label="ISBN"></el-table-column>
            <el-table-column prop="count" label="" width="50px" align="center"></el-table-column>
            <el-table-column prop="remain" label="" width="50px" align="center"></el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.row,scope.$index)">
                    </el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row,scope.$index)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="" :visible.sync="editVisible">
            <p style="color: red"></p>
            <p></p>
            <table class="el-table">
                <tr>
                    <td class="table-header"></td>
                    <td class="table-header"></td>
                    <td class="table-header">
                        
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editBook.name}}</td>
                    <td>
                        <el-input clearable :placeholder="editBook.name" v-model="changeItems.name"></el-input>
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editBook.author}}</td>
                    <td>
                        <el-input clearable :placeholder="editBook.author" v-model="changeItems.author"></el-input>

                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editBook.publishing}}</td>
                    <td>
                        <el-input clearable :placeholder="editBook.publishing"
                                  v-model="changeItems.publishing"></el-input>
                    </td>
                </tr>
                <tr>
                    <td class="table-header">ISBN</td>
                    <td>{{editBook.isbn}}</td>
                    <td>
                        <el-input clearable :placeholder="editBook.isbn" v-model="changeItems.isbn"></el-input>
                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editBook.count}}</td>
                    <td>
                        <el-input clearable :placeholder="editBook.count" type="number"
                                  v-model="changeItems.count"></el-input>

                    </td>
                </tr>
                <tr>
                    <td class="table-header"></td>
                    <td>{{editBook.remain}}</td>
                    <td>
                        <el-input clearable :placeholder="editBook.remain" type="number"
                                  v-model="changeItems.remain"></el-input>
                    </td>
                </tr>
            </table>
            <div style="text-align: center">
                <el-button type="primary" @click="handleUpdate"></el-button>
                <el-button type="danger" @click="changeItems={...emptyChangeItems}"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "BookResult",
        data() {
            return {
                result: [],
                editBook: {},
                changeItems: {},
                editVisible: false,
                emptyChangeItems: {}
            }
        },
        methods: {
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            back() {
                this.$router.go(-1);
            },
            handleEdit(row, index) {
                this.editVisible = true
                this.editBook = this.result[index]
                this.emptyChangeItems = {
                    id: row.id,
                    author: '',
                    publishing: '',
                    isbn: '',
                    count: null,
                    remain: null
                }
                this.changeItems = {...this.emptyChangeItems}
            },
            handleDelete(row, index) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete('/book/' + row.id).then((response) => {
                        // console.log(response.data)
                        alert('' + response.data + '')
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                    }).catch((error) => {
                        this.$message.error(": -> " + JSON.stringify(error))
                    })

                    this.result.splice(index, 1)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            handleUpdate() {
                let data = this.changeItems;
                console.log(data)
                this.axios.put('/book/' + data.id, data)
                    .then((response) => {
                        alert('' + response.data + '')
                        this.$message({
                            type: 'success',
                            message: '!'
                        })
                        this.editVisible=false

                    })
                    .catch((error) => {
                        this.$message({
                            type: 'error',
                            message: ": -> " + JSON.stringify(error)
                        })
                    })
            }
        },
        mounted() {
            this.result = this.$store.state.bookQueryResult
        }
    }
</script>

<style scoped>
    .table-header {
        color: darkslategrey;
        font-weight: bold;
    }
</style>