<template>
    <div class="admin-form">
        <h2 class="page-title"></h2>
        <el-form label-position="right" :model="book">
            <el-form-item label="">
                <el-input clearable v-model="book.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input clearable v-model="book.author" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input clearable v-model="book.publishing" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="ISBN">
                <el-input clearable v-model="book.isbn" placeholder="ISBN"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input clearable type="number" v-model="book.count" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input clearable type="number" v-model="book.remain" placeholder=""></el-input>
            </el-form-item>
            <el-button type="primary" @click="submitForm"></el-button>
            <el-button @click="book={...emptyBook}"></el-button>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "InputBook",
        data() {
            return {
                book: {
                    name: '',
                    author: '',
                    publishing: '',
                    isbn: '',
                    count: 0,
                    remain: 0
                },
                emptyBook: {
                    name: '',
                    author: '',
                    publishing: '',
                    isbn: '',
                    count: 0,
                    remain: 0
                }
            }
        },
        methods: {
            submitForm() {
                this.axios.post('/book', JSON.stringify(this.book))
                    .then((response) => {
                    alert('' + response.data + '')
                    this.$message({
                        type: 'success',
                        message: '!'
                    })
                }).catch((error) => {
                    this.$message.error(": -> " + JSON.stringify(error))
                })
            }
        }
    }
</script>

<style scoped>

</style>